.advantages {
  max-width: 1170px;
  padding: 0 50px;
  position: relative;

  &::before {
    width: 268px;
    height: 268px;

    position: absolute;
    top: -70px;
    left: -164px;
    content: "";
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../img/triangle.svg");
    transform: rotate(90deg);
  }

  &::after {
    width: 877px;
    height: 877px;

    position: absolute;
    bottom: -250px;
    left: 230px;
    content: "";
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url("../img/dotted_polygon.svg");
    transform: rotate(180deg);
  }

  @media (max-width: $tablet-width) {
    padding: 0 25px;

    &::before {
      display: none;
    }
  }

  @media (max-width: $mobile-width) {
    margin-top: 37px;
  }

  @media (max-width: $mobile-min-width) {
    padding: 0 16px 0 13px;
  }
}

.advantages__list {
  max-width: 1173px;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  &::after {
    width: 264px;
    height: 277px;

    position: absolute;
    top: 340px;
    right: -390px;

    content: "";
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../img/triangle.svg");
    transform: rotate(270deg);
  }
}

.advantages__item {
  width: calc(100% / 12 * 6 - 15px);
  min-height: 299px;

  margin: 0 0 30px 0;
  padding: 69px 38px 25px 43px;

  position: relative;
  box-sizing: border-box;
  z-index: 10;
  background-color: $white;
  cursor: pointer;

  border: 1px solid $border-color;
  border-left: 3px solid $main-blue;

  &--detective {
    top: 43px;
  }

  &:hover,
  &:focus {
    color: $white;
    background-color: $main-blue;
    outline: 0;
  }

  @media (max-width: $tablet-width) {
    min-height: 233px;
    padding: 34px 24px 20px;
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    min-height: 232px;
    margin: 0 0 20px;
  }

  @media (max-width: $mobile-min-width) {
    padding: 25px;
    padding-top: 39px;
    padding-bottom: 25px;

    margin-bottom: 22px;

    &:nth-child(2) {
      padding-right: 12px;
    }
  }
}

.advantages__icon {
  position: absolute;
  top: 39px;
  left: 42px;

  fill: $main-blue;

  @media (max-width: $tablet-width) {
    top: 25px;
    left: 25px;
  }
}

.advantages__item:hover .advantages__icon,
.advantages__item:focus .advantages__icon {
  fill: $white;
}

.advantages__item:hover .advantages__title,
.advantages__item:focus .advantages__title {
  color: $white;
}

.advantages__title {
  margin: 25px 0 15px;

  font-family: $font-secondary;
  font-weight: 400;
  font-size: 24px;
  color: $main-blue;
  line-height: 34px;

  @media (max-width: $tablet-width) {
    margin: 33px 0 7px;
  }

  @media (max-width: $mobile-min-width) {
    margin-bottom: 5px;

    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__text {
  margin: 0;

  font-size: 16px;
  line-height: 22px;
  color: $font-color;
}
