.form {
  width: 570px;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  z-index: 10;

  @media (max-width: $mobile-min-width) {
    flex-direction: column;
  }
}

.form__input {
  width: 270px;
  height: 50px;
  padding: 6px 16px 10px 16px;
  box-sizing: border-box;

  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  outline-color: $white;
  color: $white;

  &::-webkit-input-placeholder {
    color: $white;
  }

  &::-moz-placeholder {
    color: $white;
  }

  &:-moz-placeholder {
    color: $white;
  }

  &--text {
    width: 570px;
    height: 154px;

    margin-bottom: 43px;
    padding: 14px 16px 14px 16px;
    resize: none;
  }

  @media (max-width: $tablet-width) {
    width: 279px;
    height: 52px;
    margin: 0;

    &--text {
      width: 591px;
      height: 160px;

      margin-bottom: 43px;
    }
  }

  @media (max-width: $mobile-width) {
    width: 100%;
    margin: 0;

    &--text {
      // width: 100%;
      height: 103px;

      margin-bottom: 30px;
    }
  }
}

.form__name,
.form__phone {
  width: 270px;

  margin: 0 0 40px;
  padding: 0;

  box-sizing: border-box;

  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.form__field {
  @media (max-width: $mobile-width) {
    width: 100%;
  }
}

.form__field--question {
  margin: 0;
}

.form__consent {
  width: 240px;
  margin: 0;
  padding: 6px 9px 5px 33px;

  font-size: 14px;
  line-height: 16px;

  @media (max-width: $tablet-width) {
    width: 220px;
  }
}

.form__label-consent {
  position: relative;
  cursor: pointer;

  &::before {
    width: 16px;
    height: 16px;

    position: absolute;
    left: -33px;

    content: "";
    border: 1px solid $white;
  }
}

.form__consent input:checked + label::after {
  width: 9px;
  height: 6px;

  position: absolute;
  left: -29px;
  top: 6px;
  content: "";

  background-image: url("../img/check.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.form__button {
  width: 270px;
  min-height: 45px;

  margin: -2px 0 0;
  border: 1px solid $white;
  border-radius: 5px;

  color: $white;
  background-color: transparent;

  @media (max-width: $tablet-width) {
    width: 279px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px;
  }
}
