.sitemap {
  min-width: 450px;

  @media (max-width: $tablet-width) {
    min-width: 300px;
  }

  @media (max-width: $mobile-min-width) {
    border-bottom: 1px solid $footer-border;
  }
}

.sitemap__title {
  margin: 0;
  margin-bottom: 10px;

  font-size: 15px;
  font-weight: 400;
  letter-spacing: 0.05em;
  line-height: 16px;
  text-transform: uppercase;
  color: $main-dark-bg;
}

.sitemap__list {
  max-height: 110px;
  margin: 0;
  padding: 0;
  list-style: none;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;

  @media (max-width: $tablet-width) {
    max-height: none;
  }
}

.sitemap__button {
  display: none;
}

.sitemap__item a {
  text-decoration: none;
  font-size: 15px;
  line-height: 25px;
  color: $main-dark-bg;
  opacity: 0.5;
}
